.full-contain {
    margin: 10px;
    margin-top: 30px;
    align-content: center;
    justify-content: center;
    text-align: center;
}

@keyframes projectText {
    from {font-size: 0px;
        color:#73a2be00;}
    to {font-size: xx-large;
        color:#73a3be;}
}

.project-container {
    height: 200px;
    width: 200px;
    border: #61618b;
    border-style: dashed;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color:#73a2be00;
    font-size: 0px;
}

.project-container:hover {
    border: #b4b1bc;
    border-style: dashed;
    animation-name: projectText;
    animation-duration: 0.2s;
    animation-timing-function: ease;
    box-shadow: #d5e5ee 10px 10px 50px;
    font-size: 30px;

    color:#73a3be;

    /* background: #000000; */
    text-shadow: #435d5f 0px 0px 50px;
}

.project-description {
    margin-left: 3px;
    margin-right: 3px;

    background-color: #000000e7;
    background: linear-gradient(to left top, #777676eb,#141414e0);
    padding: 5px;
    

}

.links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 30px;

}

.p-link {
    font-family: 'Orbitron', sans-serif;
    color:#73a3be;
    text-decoration: none;
    background: linear-gradient(to top, #9c546050,  #9c546000 );
    margin: 6px;
    margin-top: 10px;
}

.p-link:hover {
    color: hsl(350, 30%, 80%);
    text-shadow: var(--link-shadow);
    animation-name: wackyShadow;
    animation-duration: 1s;
    font-weight: bold;
}

@media screen and (min-width: 1200px) {

    .project-container {
        height: 300px;
    width: 300px;
    }
}